@font-face {
    font-family: 'Pixel';
    src: url('/assets/fonts/games/tetris/Pixel-LCD-7.woff') format('woff');
}

body {
    background: url('/assets/images/games/tetris/bg.png') #000;
    background-size: cover;
}

#root {
    width:100%;overflow: auto;
}

.styled-display {
    margin-bottom: 20px;
    padding: 16px;
    color: #999;
    font-size: .8rem;
    overflow: hidden;
    white-space: nowrap;
    background-color: #000;
    border: 4px solid #333;
    font-family: Pixel, Arial, Helvetica, sans-serif;
}

.gameover {
    color: #f00;
}

.styled-button {
    display: block;
    padding: 12px;
    width: 100%;
    color: #fff;
    outline: none;
    cursor: pointer;
    font-size: .9rem;
    overflow: hidden;
    background-color: #333;
    border: 4px outset #666;
    font-family: Pixel, Arial, Helvetica, sans-serif;
}

.filled {
    border-radius: 0;
    border: 8px outset #eee;
}

.styled-button:not(:last-child) {
    margin-bottom: 20px;
}

.filled:active, #collapse:active+.filled, .pin-menu:active+.filled,
button.styled-button:active {
    border-style: inset;
}